import { makeStyles } from '@material-ui/core/styles';
import React, { useState, useCallback } from "react";
import ReactDOM from "react-dom";
import ReactPlayer from 'react-player'
import Chip from '@material-ui/core/Chip';
//import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import cubeRegularBlack1MP4 from "../assets/videos/CubeRegularBlack1.mp4";
import cylinderMegaWhite1MP4 from "../assets/videos/CylinderMegaWhite1.mp4";
import torusRegularWhite1MP4 from "../assets/videos/TorusRegularWhite1.mp4";
import cubeRegularWhite1MP4 from "../assets/videos/CubeRegularWhite1.mp4";
import torusMegaBlack1MP4 from "../assets/videos/TorusMegaBlack1.mp4";
import torusMiniBlack1MP4 from "../assets/videos/TorusMiniBlack1.mp4";


let currentVideo = cubeRegularWhite1MP4;
function changeVideo(){
  currentVideo = cubeRegularWhite1MP4;
  alert("this")
}

let initialState = {
  cube: false,
  blue: false,
  green: false,

  button1: true,
  button2: false,
  button3: false,
  button4: false,
  button5: false,
  button6: false,
};

function getHeight() {
    let divElement = document.querySelector(".boxybox");
    let elemHeight = divElement.clientHeight;
    //alert("This: " + elemHeight);
    divElement.style.height = elemHeight + "px";
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    color: '#000000 !important',
    fontWeight: '700 !important',
    border:'0px solid red',
  },
  container:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(5, 0, 5),
  },
  button:{
    width:'100%',
    justifyContent: 'center',
    padding: theme.spacing(2, 0, 2),
    border: '1px solid red',
    borderColor: '#000000',
  },
  paperFrame:{
    border: '0px solid',
    boxShadow:'0 8px 30px rgba(0,0,0,0.32)',
    borderRadius:'5px',
    p:0,
    lineHeight:0,
  },
  artDiv:{
    border:'0px solid red',

  },
  headerHero:{
    fontWeight: 800,
    fontSize:'4.8rem',
    fontFamily: 'IBM Plex Sans, sans-serif'
  },
}));

const ShapesShowcase = () => {
  const classes = useStyles();
  const [url, setUrl] = useState(cubeRegularWhite1MP4);
  const [state, setState] = useState(initialState);

  const handleClick = useCallback(
    name => event => {
      // Read the note from the react docs here
      // https://reactjs.org/docs/hooks-reference.html#functional-updates
      // Could probably even use the useReducer hook instead

      // Using the initial state here to reset all back to false
      const newState = Object.assign({}, initialState);

      // Find the one we clicked and set to true
      switch (name) {
        case "Button1":
          newState.button1 = true;
          newState.button2 = false;
          newState.button3 = false;
          newState.button4 = false;
          newState.button5 = false;
          newState.button6 = false;
          getHeight();
          setUrl(cubeRegularWhite1MP4);
          break;
        case "Button2":
          newState.button1 = false;
          newState.button2 = true;
          newState.button3 = false;
          newState.button4 = false;
          newState.button5 = false;
          newState.button6 = false;
          getHeight();
          setUrl(cubeRegularBlack1MP4);
          break;
        case "Button3":
          newState.button1 = false;
          newState.button2 = false;
          newState.button3 = true;
          newState.button4 = false;
          newState.button5 = false;
          newState.button6 = false;
          getHeight();
          setUrl(cylinderMegaWhite1MP4);
          break;
        case "Button4":
          newState.button1 = false;
          newState.button2 = false;
          newState.button3 = false;
          newState.button4 = true;
          newState.button5 = false;
          newState.button6 = false;
          getHeight();
          setUrl(torusMegaBlack1MP4);
          break;
        case "Button5":
          newState.button1 = false;
          newState.button2 = false;
          newState.button3 = false;
          newState.button4 = false;
          newState.button5 = true;
          newState.button6 = false;
          getHeight();
          setUrl(torusRegularWhite1MP4);
          break;
        case "Button6":
          newState.button1 = false;
          newState.button2 = false;
          newState.button3 = false;
          newState.button4 = false;
          newState.button5 = false;
          newState.button6 = true;
          getHeight();
          setUrl(torusMiniBlack1MP4);
          break;
        case "Cube":
          newState.cube = true;
          newState.torus = false
          newState.cross = false
          newState.diamond = false
          newState.cylinder = false
          newState.cylinder2 = false;
          getHeight();
          setUrl(cubeRegularWhite1MP4);
          break;
        case "Torus":
          newState.torus = true;
          newState.cube = false
          newState.cross = false
          newState.diamond = false
          newState.cylinder = false
          newState.cylinder2 = false;
          getHeight();
          setUrl(cubeRegularBlack1MP4);
          break;
        case "Cross":
          newState.cross = true;
          newState.cube = false
          newState.torus = false
          newState.diamond = false
          newState.cylinder = false
          newState.cylinder2 = false;
          getHeight();
          setUrl(cylinderMegaWhite1MP4);
          break;
        case "Diamond":
          newState.diamond = true;
          newState.cube = false
          newState.torus = false
          newState.cross = false
          newState.cylinder = false
          newState.cylinder2 = false;
          getHeight();
          setUrl(torusMegaBlack1MP4);
          break;
        case "Cylinder":
          newState.cylinder = true;
          newState.cube = false
          newState.torus = false
          newState.cross = false
          newState.diamond = false
          newState.cylinder2 = false;
          getHeight();
          setUrl(torusRegularWhite1MP4);
          break;
          case "Cylinder2":
            newState.cylinder2 = true;
            newState.cube = false
            newState.torus = false
            newState.cross = false
            newState.diamond = false
            newState.cylinder = false;
            getHeight();
            setUrl(torusMiniBlack1MP4);
            break;
        default:
          break;
      }
      // Update our state
      setState(newState);
    },
    [setState]
  );

  return(
    <Box>
      <Container maxWidth="md"  className={classes.container}>
        <Box m={2} p={3} pt={5}>
        <Typography gutterBottom
          align="center"
          color="text.primary"
          variant="h1"
           className={classes.headerHero}
          gutterBottom
        >
          <strong>JustShapes Art</strong>
        </Typography>
        <Typography component="h1" variant="h5" align="center" color="textPrimary" gutterBottom>
        A charity that shows how Crypto/NFTs/DeFi will change the world!<br />
        Yes, we could just give a lump sum of money away, but building something that Solana and it's community stands for is far more powerful and useful.

        </Typography>
        </Box>
      </Container>
      <Container className={classes.container} maxWidth="lg" sx={{py:5}}>
          <Grid container spacing={4} direction="row" alignItems="center" className={classes.artDiv}>
            <Grid item xs={12} sm={12} md={6} className={classes.artDiv}>
              <Container maxWidth="sm">
                <Typography component="h1" variant="h4" align="center" color="textPrimary" gutterBottom>
                  <strong>Shapes Showcase</strong>
                </Typography>
                <Typography align="center" color="textSecondary" paragraph>
                  The Shapes are created in P5JS, an open-source javascript library used by artists around the world.
                </Typography>

                  <Grid container spacing={2} >
                    <Grid item xs={12} sm={12} md={6}>
                      <Button variant="contained" color="primary" align="center" disableElevation
                        className={classes.button}
                        onClick={handleClick("Button1")}
                        variant={state.button1 ? "contained" : "outlined"}
                      >
                        Cube (Regular)
                      </Button>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6}>
                      <Button variant="contained" color="primary" align="center" disableElevation
                        className={classes.button}
                        onClick={handleClick("Button2")}
                        variant={state.button2 ? "contained" : "outlined"}
                      >
                        Cube (Regular)
                      </Button>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6}>
                      <Button variant="contained" color="primary" align="center" disableElevation
                        className={classes.button}
                        onClick={handleClick("Button3")}
                        variant={state.button3 ? "contained" : "outlined"}
                      >
                        Cylinder (Mega)
                      </Button>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6}>
                      <Button variant="contained" color="primary" align="center" disableElevation
                        className={classes.button}
                        onClick={handleClick("Button4")}
                        variant={state.button4 ? "contained" : "outlined"}
                      >
                        Torus (Mega)
                      </Button>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6}>
                      <Button variant="contained" color="primary" align="center" disableElevation
                        className={classes.button}
                        onClick={handleClick("Button5")}
                        variant={state.button5 ? "contained" : "outlined"}
                      >
                        Torus (Regular)
                      </Button>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6}>
                      <Button variant="contained" color="primary" align="center" disableElevation
                        className={classes.button}
                        onClick={handleClick("Button6")}
                        variant={state.button6 ? "contained" : "outlined"}
                      >
                        Torus (Mini)
                      </Button>
                    </Grid>
                  </Grid>
</Container>

            </ Grid>
            <Grid item xs={12} sm={12} md={6} className={classes.artDiv}>
            <div className={classes.artDiv} alignItems="center">
              <Paper className={classes.paperFrame} >
                <div className="boxybox">
                  <ReactPlayer width='100%' height='100%' playing loop muted={true} controls={false} playsinline={true} url={url} type="video/mp4" />
                </div>
              </Paper>
            </div>
            </ Grid>
          </ Grid>
      </ Container>
    </Box>
  );
}

export default ShapesShowcase;
