import { makeStyles } from '@material-ui/core/styles';
import React, { useState, useCallback } from "react";
import ReactDOM from "react-dom";
import ReactPlayer from 'react-player'
import Chip from '@material-ui/core/Chip';
//import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import cubeRegularWhite1 from "../assets/images/cubeRegularWhite1.jpeg";
import cylinderMegaBlack1 from "../assets/images/cylinderMegaBlack1.jpeg";
import cubeMiniBlack1 from "../assets/images/cubeMiniBlack1.jpeg";
import torusSingleWhite1 from "../assets/images/torusSingleWhite1.jpeg";
import cyclinderMiniWhite1 from "../assets/images/cyclinderMiniWhite1.jpeg";
import cubeRegularWhite2 from "../assets/images/cubeRegularWhite2.jpeg";



const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    color: '#000000 !important',
    fontWeight: '700 !important',
    border:'0px solid red',
  },
  box:{
    backgroundColor: '#fafafa !important',
  },
  container:{
    backgroundColor: '#fafafa !important',
    padding: theme.spacing(5, 0, 5),
  },
  containerHeader:{
    padding: theme.spacing(5, 0, 5),
  },
  button:{
    width:'100%',
    justifyContent: 'center',
    padding: theme.spacing(2, 0, 2),
    border: '1px solid red',
    borderColor: '#000000',
  },
  paperFrame:{
    border: '1px solid 000000',
    boxShadow:'0 8px 10px rgba(0,0,0,0.15)',
    borderRadius:'5px',
    p:0,
    lineHeight:0,
  },
  paperTitle:{
    padding: theme.spacing(2, 0,1),
  },
  artDiv:{
      border:'0px solid red',
      width:'100%',
    },
  chipCommon:{
    margin: theme.spacing(0,0, 0,0),
    backgroundColor: '#000000',
    color:'#ffffff',
  },
  chipUncommon:{
    margin: theme.spacing(0,0, 0,0),
    backgroundColor: '#1FB254',
    color:'#ffffff',
  },
  chipRare:{
    margin: theme.spacing(0,0, 0,0),
    backgroundColor: '#007CF3',
    color:'#ffffff',
  },
  chipLegendary:{
    margin: theme.spacing(0,0, 0,0),
    backgroundColor: '#9c78ff',
    color:'#ffffff',
  },
  chipExotic:{
    margin: theme.spacing(0,0, 0,0),
    backgroundColor: '#FFBB25',
    color:'#ffffff',
  },

}));

const ShapesDescription = () => {
  const classes = useStyles();

  return(
    <Box className={classes.box}>
      <Container className={classes.container} maxWidth="lg">

        <Container maxWidth="sm" className={classes.containerHeader}>
                <Typography component="h1" variant="h4" align="center" color="textPrimary" gutterBottom>
                  <strong>Shapes Sizes</strong>
                </Typography>
                <Typography align="center" color="textSecondary" paragraph>
                  The Shapes are created in P5JS, an open-source javascript library used by artists around the world.
                </Typography>
          </Container>
                <div className={classes.heroButtons}>
                  <Grid container spacing={10} >
                    <Grid item xs={12} sm={12} md={3} align="center">
                      <Paper className={classes.paperFrame} >
                        <img width='100%' height='100%' src={torusSingleWhite1} alt="Thug" />
                      </Paper>
                      <Typography component="h1" variant="h5" align="center" color="textPrimary" className={classes.paperTitle} >
                        Single
                      </Typography>
                      <Typography variant="p" align="center" color="textSecondary" gutterBottom>

                      </Typography>
                      <Chip className={classes.chipExotic} label="Exotic" />
                    </Grid>
                    <Grid item xs={12} sm={12} md={3} align="center">
                      <Paper className={classes.paperFrame} >
                        <img width='100%' height='100%' src={cyclinderMiniWhite1} alt="Thug" />
                      </Paper>
                      <Typography component="h1" variant="h5" align="center" color="textPrimary" className={classes.paperTitle} >
                        Mini
                      </Typography>
                      <Typography variant="p" align="center" color="textSecondary" gutterBottom>

                      </Typography>
                      <Chip className={classes.chipRare} label="Rare" />
                    </Grid>
                    <Grid item xs={12} sm={12} md={3} align="center">
                      <Paper className={classes.paperFrame} >
                        <img width='100%' height='100%' src={cubeRegularWhite2} alt="Thug" />
                      </Paper>
                      <Typography component="h1" variant="h5" align="center" color="textPrimary" className={classes.paperTitle} >
                        Regular
                      </Typography>
                      <Typography variant="p" align="center" color="textSecondary" gutterBottom>

                      </Typography>
                      <Chip className={classes.chipUncommon} label="Uncommon" />
                    </Grid>
                    <Grid item xs={12} sm={12} md={3} align="center">
                      <Paper className={classes.paperFrame} >
                        <img width='100%' height='100%' src={cylinderMegaBlack1} alt="Thug" />
                      </Paper>
                      <Typography component="h1" variant="h5" align="center" color="textPrimary" className={classes.paperTitle} >
                        Mega
                      </Typography>
                      <Typography variant="p" align="center" color="textSecondary" gutterBottom>

                      </Typography>
                      <Chip className={classes.chipCommon} label="Common" />
                    </Grid>
                  </Grid>
                </div>
      </ Container>
    </Box>
  );
}

export default ShapesDescription;
